html {
    overflow: hidden;
}


.App {
    text-align: center;
    color: #fff;
    width: 100vw;
    height: 100vh;
/*    background-image: url(https://github.githubassets.com/assets/hero-glow-f6eed469bca2.svg);*/
/*    radial-gradient(ellipse at -60% 20%, #042D6D 0%, transparent 60%),*/
/*                        radial-gradient(ellipse at 90% 20%, #042D6D 0%, transparent 100%)*/

/*  background: radial-gradient(ellipse at 20% 80%, #f00 0%, #e1e4e8 62%, #d1d6db 100%);*/
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}


